.music-player-container {
  padding: 1rem;
  margin-top: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.media-control-card-container {
  width: fit-content;
  border: 3px solid black;
  border-radius: 0.5rem;
}

.loading {
  padding: 1rem;
  font-size: 5rem;
}

.connection-instructions {
  padding: 1rem;
  font-size: 1.5rem;
}

/* For mobile */
@media (max-width: 599px) {
  .connection-instructions {
    padding: 0.5rem;
  }

  .loading {
    padding: 0.5rem;
  }
  .album-cover {
    border: 3px solid black;
    width: 350px;
    height: 350px;
  }
}

/* For tablet in portrait mode */
@media (min-width: 600px) {
  .album-cover {
    border: 3px solid black;
    width: 400px;
    height: 400px;
  }
}

/* For tablet in landscape mode */
@media (min-width: 900px) {
  .album-cover {
    border: 3px solid black;
    width: 500px;
    height: 500px;
  }
}

/* For desktop */
@media (min-width: 1200px) {
  .album-cover {
    border: 3px solid black;
    width: 625px;
    height: 625px;
  }
}

@media (min-width: 1800px) {
  .album-cover {
    border: 3px solid black;
    width: 800px;
    height: 800px;
  }
}
